@import './fonts-and-colors.scss';

// Reset
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Ubuntu', sans-serif;
    font-weight: 300;
}

h1 {
    font-weight: 700;
    color: $primary-font-color;
    font-size: 2.5rem;
}

h2 {
    font-weight: 400;
    color: $primary-font-color;
}

h3 {
    font-weight: 400;
    color: $primary-font-color;
}

p {
    font-size: 1.15rem;
    color: $secondary-font-color;
}

a {
    user-select: none;
}

.link-set {
    .icon {
        transition: all 150ms ease-in-out;

        &:hover {
            scale: 1.25;
            color: $green;
        }
    }
}

button {
    display: inline-block;
    padding: 1rem 2rem;
    font-size: 1rem;
    text-align: center;
    text-decoration: none;
    letter-spacing: 1px;
    border: none;
    background-color: $greenGlass50;
    color: $primary-font-color;
    cursor: pointer;
    transition: all 150ms ease-in-out;

    &:hover {
        background-color: $green;
        color: white;
    }
}

header {
    display: flex;
    position: fixed;
    top: 0rem;
    z-index: 1000;
    width: 100%;
    align-items: center;
    flex-direction: column;
}

main {
    //border: 5px solid black;
    margin-top: 91px;
}

footer {
    margin-top: 4rem;
}

.page {
    display: flex;
    flex-direction: column;
    width: 100%;

    .head-section {
        display: flex;
        //justify-content: center;
        //border: 5px solid red;
        width: 100%;
        height: 500px;
        background-size: cover;
        background-repeat: no-repeat;

        .head-content-container {
            //display: flex;
            //border: 5px solid yellow;
            //justify-content: center;
            //align-items: center;
            width: 100%;
            //height: 100%;

            .head-content-box {
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 80%;
                padding: 2rem;
                //border: 5px solid blue;
                margin: auto;

                h1 {
                    margin-bottom: 2rem;
                }

                p {
                    max-width: 400px;
                    line-height: 1.5rem;
                    margin-bottom: 2rem;
                }

                p,
                h1 {
                    position: relative;
                    z-index: 100;
                }


                .link-box {
                    display: flex;
                    flex-wrap: wrap;

                    button {
                        margin-right: 0.75rem;
                        padding: 0.25rem 0.35rem;
                    }
                }

                .amobea {
                    width: 550px;
                    position: relative;
                    margin-top: -350px;
                    margin-left: -100px;
                    position: relative;
                    display: none;
                }

                .faq {
                    width: 45%;
                    margin-bottom: -100px;
                    position: relative;
                }
            }
        }
    }

    .section {
        display: flex;
        justify-content: center;
        width: 100%;
        background-size: cover;
        background-repeat: no-repeat;

        .content-container {
            display: flex;
            width: 100%;
            align-items: center;
            //border: 4px solid blue;

            .content-box {
                display: flex;
                width: 80%;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding: 2rem;
                //border: 4px solid yellowgreen;
            }
        }
    }
}

