@import url("https://fonts.googleapis.com/css2?family=Cardo:wght@400;700&family=EB+Garamond:wght@400;500&family=Ubuntu:wght@300;400&display=swap");
/*
font-family: 'Cardo', serif;
font-family: 'EB Garamond', serif;
font-family: 'Ubuntu', sans-serif;
$orange: rgb(255, 165, 0);
$navy: rgb(0, 0, 128);
*/
@import './kezdolap.css';
@import './szolgaltatasok.css';
@import './arak.css';
@import './magamrol.css';
@import './kapcsolat.css';
@import './galeria.css';
@import './jo_tudni.css';
@import './akciok.css';
@import './gyik.css';
@import './felhasz_felt.css';
@import './navigation.css';
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
}

h1 {
  font-weight: 700;
  color: rgb(80, 80, 80);
  font-size: 2.5rem;
}

h2 {
  font-weight: 400;
  color: rgb(80, 80, 80);
}

h3 {
  font-weight: 400;
  color: rgb(80, 80, 80);
}

p {
  font-size: 1.15rem;
  color: rgb(100, 100, 100);
}

a {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.link-set .icon {
  transition: all 150ms ease-in-out;
}
.link-set .icon:hover {
  scale: 1.25;
  color: rgb(117, 189, 128);
}

button {
  display: inline-block;
  padding: 1rem 2rem;
  font-size: 1rem;
  text-align: center;
  text-decoration: none;
  letter-spacing: 1px;
  border: none;
  background-color: rgb(186, 222, 191);
  color: rgb(80, 80, 80);
  cursor: pointer;
  transition: all 150ms ease-in-out;
}
button:hover {
  background-color: rgb(117, 189, 128);
  color: white;
}

header {
  display: flex;
  position: fixed;
  top: 0rem;
  z-index: 1000;
  width: 100%;
  align-items: center;
  flex-direction: column;
}

main {
  margin-top: 91px;
}

footer {
  margin-top: 4rem;
}

.page {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.page .head-section {
  display: flex;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-repeat: no-repeat;
}
.page .head-section .head-content-container {
  width: 100%;
}
.page .head-section .head-content-container .head-content-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  padding: 2rem;
  margin: auto;
}
.page .head-section .head-content-container .head-content-box h1 {
  margin-bottom: 2rem;
}
.page .head-section .head-content-container .head-content-box p {
  max-width: 400px;
  line-height: 1.5rem;
  margin-bottom: 2rem;
}
.page .head-section .head-content-container .head-content-box p,
.page .head-section .head-content-container .head-content-box h1 {
  position: relative;
  z-index: 100;
}
.page .head-section .head-content-container .head-content-box .link-box {
  display: flex;
  flex-wrap: wrap;
}
.page .head-section .head-content-container .head-content-box .link-box button {
  margin-right: 0.75rem;
  padding: 0.25rem 0.35rem;
}
.page .head-section .head-content-container .head-content-box .amobea {
  width: 550px;
  position: relative;
  margin-top: -350px;
  margin-left: -100px;
  position: relative;
  display: none;
}
.page .head-section .head-content-container .head-content-box .faq {
  width: 45%;
  margin-bottom: -100px;
  position: relative;
}
.page .section {
  display: flex;
  justify-content: center;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}
.page .section .content-container {
  display: flex;
  width: 100%;
  align-items: center;
}
.page .section .content-container .content-box {
  display: flex;
  width: 80%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.bejelentkezes,
.nyitvatartas,
.cim {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  margin: 1rem;
}
.bejelentkezes h2,
.nyitvatartas h2,
.cim h2 {
  margin-bottom: 1rem;
}

.bejelentkezes a:not(a:last-child) {
  margin-bottom: 0.5rem;
}
.bejelentkezes a {
  display: flex;
  align-items: center;
  color: black;
}
.bejelentkezes a .icon {
  margin-right: 1rem;
}
.bejelentkezes a {
  border-bottom: 2px solid transparent;
  transition: all 150ms ease-in-out;
  color: rgb(80, 80, 80);
}
.bejelentkezes a:hover {
  border-color: rgb(100, 100, 100);
}

.nyitvatartas ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  width: 100%;
}
.nyitvatartas ul li:not(li:last-child) {
  margin-bottom: 0.5rem;
}
.nyitvatartas ul li {
  display: flex;
  justify-content: center;
  min-width: 250px;
}
.nyitvatartas ul li span:first-child {
  margin-right: 1rem;
}

.cim a {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 2px solid transparent;
  transition: all 150ms ease-in-out;
  color: rgb(80, 80, 80);
}
.cim a:hover {
  border-color: rgb(100, 100, 100);
}
.cim a p {
  font-size: 1rem;
}
.cim a p:not(p:last-child) {
  margin-bottom: 0.5rem;
}/*# sourceMappingURL=App.css.map */